.stars {
  display: flex;
  align-items: center;

  &__star {
    background-image: url(../images/star.svg);
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    margin-right: 4px;
    background-position: 50%;
  }
}

.stars :nth-child(-n + 4) {
  background-image: url(../images/star-active.svg);
}
